@import "reset";
@import "variables";

* {
  box-sizing: border-box;
}
body {
  height: 104vh;
  font-family: RingsideExtraWide-Book,Blank,Helvetica Neue,Helvetica,sans-serif;
}
header {
  background-color: #f0eff0;
  width: 39%;
  height: 100%;
  padding: 180px;
  padding: 10vh 0 72px 72px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: $red;
  h1 {
    text-transform: uppercase;
    font-size: 4.6rem;
    width: 50%;
  }
  h3 {
    font-size: 23px;
    font-weight: 300;
    width: 60%;
    margin-bottom: 20px;
  }

  h3, p{
    line-height: 1.3;
  }
  p {
    width: 70%;
    text-align: justify;
    font-weight: 300;
    font-size: 17px;
    margin-bottom: 28px;
  };

  nav {
    width: 60%;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        margin-right: 20px;
        font-size: 14px;
        color: black;
        opacity: 0.5;
        padding-bottom: 5px;
        border-bottom: 2px solid rgba(0, 0, 0, 1);
        margin-bottom: 3px;
        &:hover {
          opacity: 1;
        }
      }
    }
  }
}